        "articleTitle": "The Sputnik V Covid-19 vaccine",
        "articleBody": "<p>The Russian Direct Investment Fund (RDIF) informed on Saturday that the production in India of Sputnik V, the Russia-made vaccine against the coronavirus disease (Covid-19) is expected to come \"fully on stream\" in September.</p><p>\"Production in India is expected to come fully on stream in September. RDIF expects India to become a major production hub of Sputnik V,\" a statement from RDIF, Russia's sovereign wealth fund, that markets the vaccine globally, said. The statement also mentioned various Indian pharmaceutical firms with which RDIF has tied up to manufacture the shot in India. These are the Serum Institute of India (SII), Hetero Biopharma, Gland Pharma, Panacea Biotec, Stelis Biopharma, Virchow Biotech and Morepen Laboratories.</p>",
