        "_id": "61057784787d4f32c07d4f56",
        "articleTitle": "Mumbai: BMC to appoint consultant",
        "articleBody": "<p><span style=\"color: rgb(66, 66, 66);\">To prevent disasters at landslide-prone sides across Mumbai, some of which are on the land owned by the collector or Maharashtra Housing and Area Development Authority (Mhada), the Brihanmumbai Municipal Corporation (BMC) has decided to appoint a consultant to survey these sites and suggest corrective measures. These measures will be suggested to the respective agencies who own such land and will be implemented by the agencies. While BMC will bear the cost of appointing a consultant, the construction or work cost will be borne by the respective agencies.</span></p>",
