        "articleTitle": "Aakash Chopra and Rashid Khan",
        "articleBody": "<p>Team India might have lost the T20 international series to Sri Lanka but there have been several positives to come out of the three-match contest. Prithvi Shaw looked in great touch, Suryakumar Yadav continued to build his case for a permanent spot, Kul-Cha bowled in tandem, Deepak Chahar impressed and Rahul Chahar built his case for selection.</p><p>Even though the Indian team were faced with adversity due to Covid-19, the squad continued to fight on against Sri Lanka. Former India batsman Aakash Chopra was impressed with Rahul's performance in the series and said that he reminded him of Rashid Khan. In the 3rd T20I, Rahul picked up three wickets and constantly troubled the Sri Lanka batsmen.</p>",
