        "articleBody": "<p><span style=\"color: rgb(66, 66, 66);\">“Rahul Chahar doesn’t play regularly in the Team India squad since Yuzvendra Chahal is the No. 1 leg-spinner.&nbsp;</span><a href=\"https://www.hindustantimes.com/topic/Ravindra-Jadeja\" rel=\"noopener noreferrer\" target=\"_blank\" style=\"color: rgb(0, 177, 205);\">Ravindra Jadeja</a><span style=\"color: rgb(66, 66, 66);\">&nbsp;and Washington Sundar are also other spin options Team India have. Varun Chakravarthy is also trying to make a name as a mystery spinner. But Chahar has grabbed every opportunity that has come his way to play for Team India,\" Chopra said on his Youtube Channel.</span></p>",
