        "articleTitle": "Sri Lanka great Kumar Sangakkara commented on Stokes",
        "articleBody": "<p>Stokes had rushed back from injury to lead England in a one-day international series against Pakistan earlier this month after all those originally selected were ruled out by a coronavirus outbreak within the hosts' camp.</p><p>The 24-year-old's struggles followed those of Japanese tennis star Naomi Osaka, another face of the Games who lost in the third round on her return from a mental health break, having withdrawn from the French Open and skipped Wimbledon saying she had been battling depression and anxiety.</p>",
