        "articleTitle": "Aussie legend Glenn McGrath reacts to Chetan Sakariy",
        "articleBody": "<p>Australia great Glenn McGrath is 'proud' of India's fast-bowling duo of Chetan Sakariya and Sandeep Warrier, both of whom made their international debuts during the recently-concluded India vs Sri Lanka series. Sakariya was the first to make his India debut, getting a game in the final ODI of the series, while Warrier received his India cap in the final T20I.</p><p>\"A huge congratulations to both @sakariya.chetan &amp; Sandeep Warrier for making their debut for India @_official_bcci_ So proud of you both,\" McGrath tweeted.</p>",
